*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

img {
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
  color: inherit;
}

/* Base Typography Styles */
body {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  color: #091133;
  line-height: 1.6;
}

/* Headings */
h2 {
  font-size: 3.125rem;
  font-weight: 700;
  margin-bottom: 8px;
}

h3 {
  font-size: 2.25rem;
  font-weight: 600;
  margin-bottom: 4px;
}

/* Paragraphs */
p {
  font-size: 1rem;
  margin-bottom: 8px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.button {
  display: inline-block;
  padding: 12px 24px;
  font-weight: 600;
  border-radius: 4px;
  transition: background-color 0.3s ease;
  width: 189px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  font-family: "Inter", sans-serif;
}
@media (max-width: 768px) {
  .button {
    width: 160px;
    padding: 10px 20px;
    font-size: 15px;
  }
}
@media (max-width: 576px) {
  .button {
    width: 140px;
    padding: 8px 16px;
    font-size: 14px;
  }
}
@media (max-width: 480px) {
  .button {
    width: auto;
    padding: 6px 12px;
    font-size: 13px;
  }
}

.button--primary {
  background-color: #111b47;
  color: #ffffff;
  border: none;
}
.button--primary:hover {
  background-color: #0f1a6a;
}

.button--secondary {
  background-color: transparent;
  color: #091133;
  border: 2px solid #091133;
}
.button--secondary:hover {
  background-color: rgba(231, 236, 255, 0.5019607843);
  color: #091133;
}

.buy-button {
  display: inline-flex;
  align-items: center;
  justify-content: left;
  padding: 10px 80px;
  background-color: #091133;
  border: 2px solid #091133;
  border-radius: 4px;
  font-weight: 600;
  color: #ffffff;
  font-size: 16px;
  font-family: "Inter", sans-serif;
  transition: background-color 0.3s ease;
}
.buy-button:hover {
  background-color: #007bff;
  color: #ffffff;
}
@media (max-width: 768px) {
  .buy-button {
    padding: 10px 40px;
    font-size: 15px;
  }
}
@media (max-width: 576px) {
  .buy-button {
    padding: 10px 24px;
    font-size: 14px;
  }
}
@media (max-width: 480px) {
  .buy-button {
    padding: 8px 20px;
    font-size: 13px;
  }
}

.features {
  padding: 5rem 5.5rem;
  background-color: #ffffff;
}
.features .feature {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 80px;
  gap: 40px;
}
.features .feature-text {
  flex: 1;
}
.features .feature-text h3 {
  font-size: 28px;
  color: #111b47;
  margin-bottom: 20px;
}
.features .feature-text p {
  font-size: 16px;
  color: #6c757d;
  line-height: 1.6;
  margin-bottom: 20px;
}
.features .feature-text .feature-cards {
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
}
.features .feature-text .feature-cards .feature-card {
  flex: 1 1 250px;
  background-color: #f8f9ff;
  padding: 24px;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.03);
}
.features .feature-text .feature-cards .feature-card img {
  width: 40px;
  height: 40px;
  margin-bottom: 10px;
}
.features .feature-text .feature-cards .feature-card h4 {
  font-size: 18px;
  margin-bottom: 10px;
}
.features .feature-text .feature-cards .feature-card p {
  font-size: 14px;
  color: #6c757d;
}
.features .feature-image {
  flex: 1;
}
.features .feature-image img {
  width: 100%;
  height: auto;
  display: block;
}
.features .feature.feature-secondary {
  flex-direction: row-reverse;
}
.features .feature.feature-last {
  flex-direction: row;
}
@media (max-width: 768px) {
  .features .feature {
    flex-direction: column;
  }
  .features .feature .feature-text,
  .features .feature .feature-image {
    text-align: center;
  }
  .features .feature .feature-cards {
    justify-content: center;
  }
}

.site-footer {
  background-color: #e7ecff;
  padding: 40px 80px;
  font-family: "Inter", sans-serif;
}
.site-footer .container {
  padding-left: 0;
  padding-right: 0;
  max-width: 100%;
}
.site-footer .footer-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  margin-bottom: 20px;
}
.site-footer .footer-left {
  color: #6c757d;
  font-size: 14px;
}
.site-footer .footer-logo {
  font-weight: 700;
  font-size: 18px;
  color: #091133;
}
.site-footer .footer-button {
  background-color: #091133;
  color: white;
  font-size: 12px;
  padding: 6px 12px;
  border-radius: 2px;
  text-decoration: none;
  transition: background-color 0.3s ease;
}
.site-footer .footer-button:hover {
  background-color: #007bff;
}
.site-footer .footer-divider {
  border: none;
  border-top: 1px solid #ccc;
  margin: 20px 0;
}
.site-footer .footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}
.site-footer .footer-nav {
  display: flex;
  gap: 30px;
  list-style: none;
  padding: 0;
  margin: 0;
}
.site-footer .footer-nav a {
  color: #6c7cb2;
  font-size: 14px;
  transition: color 0.3s;
}
.site-footer .footer-nav a:hover {
  color: #091133;
}
.site-footer .footer-social {
  display: flex;
  align-items: center;
  gap: 16px;
}
.site-footer .footer-social a {
  display: inline-block;
  opacity: 0.6;
  transition: opacity 0.3s;
}
.site-footer .footer-social a:hover {
  opacity: 1;
}
.site-footer .footer-social a img {
  width: 20px;
  height: 20px;
  display: block;
}
@media (max-width: 992px) {
  .site-footer .footer-top {
    flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    gap: 30px;
  }
  .site-footer .footer-left,
  .site-footer .footer-logo,
  .site-footer .footer-button {
    flex: 1 1 10%;
    margin-bottom: 10px;
  }
  .site-footer .footer-bottom {
    flex-direction: row;
    align-items: center;
  }
  .site-footer .footer-nav {
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media (max-width: 576px) {
  .site-footer {
    padding: 30px 20px;
  }
  .site-footer .footer-top {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 16px;
  }
  .site-footer .footer-button {
    padding: 6px 10px;
    font-size: 11px;
  }
  .site-footer .footer-nav {
    gap: 16px;
  }
}
@media (max-width: 480px) {
  .site-footer .footer-social {
    flex-wrap: wrap;
    justify-content: center;
  }
}

.site-header {
  background-color: transparent;
  box-shadow: none;
  padding: 1.25rem 5.5rem;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
  font-family: "Inter", sans-serif;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}
.site-header.scrolled {
  background-color: #ffffff;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
}
.site-header .wide-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 20px;
}
.site-header .site-nav {
  flex: 1;
}
.site-header .site-nav ul {
  display: flex;
  gap: 40px;
  list-style: none;
  padding: 0;
  margin: 0;
}
.site-header .site-nav ul li a {
  text-decoration: none;
  color: #111b47;
  font-size: 16px;
  transition: color 0.3s;
}
.site-header .site-nav ul li a:hover {
  color: #091133;
}
.site-header .logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  font-size: 22px;
  font-weight: 700;
  color: #111b47;
  text-align: center;
}
.site-header .logo a {
  text-decoration: none;
  color: inherit;
}
.site-header .buy-button {
  background-color: #111b47;
  color: #ffffff;
  border: none;
  padding: 10px 24px;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 600;
  text-decoration: none;
  transition: background-color 0.3s;
}
.site-header .buy-button:hover {
  background-color: #0f1a6a;
}
@media (max-width: 576px) {
  .site-header .buy-button {
    padding: 8px 18px;
    font-size: 14px;
  }
}

.hero {
  background-image: url("../img/Hero.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  display: flex;
  align-items: center;
  text-align: justify;
  padding: 0 5.5rem;
  font-family: "Inter", sans-serif;
}
.hero .hero-content {
  max-width: 600px;
  color: #091133;
}
.hero .hero-content h1 {
  font-size: 48px;
  margin-bottom: 16px;
}
.hero .hero-content p {
  font-size: 18px;
  color: #6c757d;
  margin-bottom: 24px;
}
.hero .hero-content .button-group {
  display: flex;
  justify-content: center;
  gap: 8px;
}

.pricing-container1 {
  position: relative;
  background-color: rgba(231, 236, 255, 0.5019607843);
  overflow: hidden;
  text-align: center;
  padding: 5rem 5.5rem;
}
.pricing-container1 .container {
  padding-left: 0 !important;
  padding-right: 0 !important;
  max-width: 100% !important;
}
.pricing-container1 .pricing-top-wave {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 150px;
  display: block;
  z-index: 0;
  pointer-events: none;
  background-color: #ffffff;
}
.pricing-container1 .pricing-container {
  position: relative;
  z-index: 1;
  max-width: 700px;
  margin: 0 auto;
  padding: 200px 5% 100px;
}

.pricing h2 {
  font-size: 32px;
  font-weight: 700;
  color: #091133;
  margin-bottom: 20px;
}

.pricing-description {
  color: #6c7cb2;
  font-size: 16px;
  line-height: 1.6;
  margin-bottom: 40px;
}

.price-amount .price {
  font-size: 48px;
  font-weight: 700;
  color: #091133;
  display: block;
}

.price-amount .price-desc {
  font-size: 16px;
  margin-top: 4px;
  color: #091133;
}

.pricing-subtext {
  color: #6c757d;
  font-size: 14px;
  margin-top: 30px;
}

.pricing-button {
  display: inline-block;
  margin-top: 20px;
  background-color: #091133;
  color: white;
  font-weight: 600;
  padding: 10px 24px;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}
.pricing-button:hover {
  background-color: #007bff;
}

@media (max-width: 768px) {
  .pricing-container1 .pricing-container {
    padding: 160px 5% 80px;
  }
  .pricing-container1 .pricing-top-wave {
    height: 100px;
  }
  .pricing-container1 h2 {
    font-size: 24px;
  }
  .pricing-container1 .price-amount .price {
    font-size: 36px;
  }
}